*{
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
    
}

@font-face {
  font-family: ttFont;
  src: url('../../assets/static/Orbitron-Black.ttf');
}

#myVideo {
  position: absolute;
  height: 100%;
  width: 90%;
  bottom: 0;
  margin-left: 5%;
    }

.First_Page{
    transition: 0.6s ease-in;
    background-repeat: no-repeat;
    color: white;
    display: block;
    margin-top: 6%;
    margin-bottom: 0;
    padding-bottom: 2.6%;
    
}




/******* Heading Animation **********/
.Heading{
    color: #fffdfd;
    padding: 12% 0 0vw 66%;
    font-family: ttFont;
    z-index:1; 
}
.title{
  text-shadow: 6px -4px black;
  padding-top: 1%;

}
li{
    display: inline;
    font-size: 300%;
    color: rgb(196, 183, 0);
}
      /*   1   */
  #_1 {
    animation: _1 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 2s both;
  }
  @keyframes _1 {
    0% { opacity: 0; transform: translateY(-200px); }
    100% { opacity: 1;  transform: translateY(0px); }
  }
      /*   2   */
  #_2 {    
    animation: _2 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 2s both;
  }
  @keyframes _2 {
    0% { opacity: 0; }
    100% {  opacity: 1; }
  }
      /*  3  */
  #_3 {    
      font-size: 3rem;
    animation: _3 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 2s both;
  }
  @keyframes _3 { 
      0% { transform: translateY(-20px); opacity: 0;  }
    100% { transform: translateY(0); opacity: 1;  }
  }
      /*   4   */
  #_4 {
      font-size: 3rem;
    animation: _4 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 2s both;
  }
  @keyframes _4 {
    0% { transform: translateY(-20px);  opacity: 0;   }
    100% { transform: translateY(0); opacity: 1;   }
  }
  
  /*****/
  
.First-page-button {
  position: relative;
  margin-top: 23vh;
  margin-left: 47vw;
  margin-bottom: 5vh;
  background-color: rgba(12, 11, 11, 0.651); 
  text-align: center;
  font-size: 3vw;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  animation-name: pulse;
  animation-duration: 1s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  line-height: 4vw;
  border: solid #eeeeeeab;
  box-shadow: 0 0 20px 3px #080808f1;
}
.First-page-button a{
  color: #888585;
  text-decoration: none;
}
.First-page-button :hover{
  color: #eeeeee;
  text-align: center;
  border-radius: 50%;

}
@keyframes pulse{ 
  0%{ font-size: 2.5vw;
      width: 55px;
      height: 55px;
      box-shadow: 0 0 30px 6px #080808f1;
  }
  100%{   font-size: 3vw;
          width: 55px;
          height: 55px;
          box-shadow: 0 0 8px 3px #000000;
  }
}